/* eslint-disable @typescript-eslint/no-unused-vars */ 
import { useState } from "react";
import { Table, TableBody, TableCell, TableHead, TableHeader, TableRow } from "@/components/ui/table";
import { Card, CardContent, CardHeader, CardTitle } from "@/components/ui/card";
import { Badge } from "@/components/ui/badge";
import { Button } from "@/components/ui/button";
import { Input } from "@/components/ui/input";
import { 
  Edit2, 
  Users, 
  Search, 
  AlertCircle, 
  CheckCircle2, 
  Clock, 
  XCircle, 
  CalendarClock,
  Trash2 
} from "lucide-react";
import { Tooltip, TooltipContent, TooltipProvider, TooltipTrigger } from "@/components/ui/tooltip";
import { Alert, AlertDescription } from "@/components/ui/alert";
import {
  AlertDialog,
  AlertDialogAction,
  AlertDialogCancel,
  AlertDialogContent,
  AlertDialogDescription,
  AlertDialogFooter,
  AlertDialogHeader,
  AlertDialogTitle,
  AlertDialogTrigger,
} from "@/components/ui/alert-dialog";

interface Soutenance {
  id: number;
  date: string;
  time: string;
  location: string;
  groupe: string;
  juryNames: string | string[];
  status: "Scheduled" | "Completed" | "Pending" | "Cancelled" | string;
}

interface SoutenanceTableProps {
  soutenances?: Soutenance[];
  onEdit?: (id: number) => void;
  onDelete?: (id: number) => void;
}

export default function SoutenanceTable({ 
  soutenances = [], 
  onEdit = (id) => console.log(`Edit soutenance ${id}`),
  onDelete = (id) => console.log(`Delete soutenance ${id}`)
}: SoutenanceTableProps) {
  const [searchTerm, setSearchTerm] = useState("");

  const filteredSoutenances = soutenances.filter(soutenance => 
    soutenance.groupe.toLowerCase().includes(searchTerm.toLowerCase()) ||
    (typeof soutenance.juryNames === "string" 
      ? soutenance.juryNames.toLowerCase().includes(searchTerm.toLowerCase())
      : soutenance.juryNames.some(name => name.toLowerCase().includes(searchTerm.toLowerCase())))
  );

  return (
    <Card className="w-full text-lg p-6">
      <CardHeader>
        <div className="flex items-center justify-between">
          <CardTitle className="text-2xl">Soutenances</CardTitle>
          <div className="relative w-80">
            <Search className="absolute left-3 top-3 h-6 w-6 text-muted-foreground" />
            <Input
              placeholder="Rechercher..."
              value={searchTerm}
              onChange={(e) => setSearchTerm(e.target.value)}
              className="pl-10 text-lg h-12"
            />
          </div>
        </div>
      </CardHeader>
      <CardContent className="p-0">
        <div className="relative w-full overflow-auto">
          <Table className="text-lg">
            <TableHeader className="bg-muted/50 text-xl">
              <TableRow>
                <TableHead className="w-[200px] font-bold">Date</TableHead>
                <TableHead className="w-[120px] font-bold">Heure</TableHead>
                <TableHead className="w-[180px] font-bold">Lieu</TableHead>
                <TableHead className="w-[150px] font-bold">Groupe</TableHead>
                <TableHead className="w-[250px] font-bold">Jury</TableHead>
                <TableHead className="w-[150px] font-bold">Statut</TableHead>
                <TableHead className="w-[120px] font-bold">Actions</TableHead>
              </TableRow>
            </TableHeader>
            <TableBody>
              {filteredSoutenances.map((soutenance) => (
                <TableRow key={soutenance.id} className="hover:bg-muted/50 text-xl h-16">
                  <TableCell className="font-semibold">
                    {new Date(soutenance.date).toLocaleDateString("fr-FR", {
                      day: "numeric",
                      month: "long",
                      year: "numeric",
                    })}
                  </TableCell>
                  <TableCell>{soutenance.time}</TableCell>
                  <TableCell>{soutenance.location}</TableCell>
                  <TableCell className="font-semibold">{soutenance.groupe}</TableCell>
                  <TableCell className="truncate max-w-[250px]">
                    {Array.isArray(soutenance.juryNames) ? soutenance.juryNames.join(", ") : soutenance.juryNames}
                  </TableCell>
                  <TableCell>
                    <Badge className="text-xl px-4 py-2">
                      {soutenance.status}
                    </Badge>
                  </TableCell>
                  <TableCell>
                    <div className="flex items-center gap-4">
                      <Button variant="ghost" size="icon" className="h-10 w-10" onClick={() => onEdit(soutenance.id)}>
                        <Edit2 className="h-6 w-6" />
                      </Button>
                      <Button variant="ghost" size="icon" className="h-10 w-10 text-red-600" onClick={() => onDelete(soutenance.id)}>
                        <Trash2 className="h-6 w-6" />
                      </Button>
                    </div>
                  </TableCell>
                </TableRow>
              ))}
            </TableBody>
          </Table>
        </div>
      </CardContent>
    </Card>
  );
}
